const initRubber = () => {
    const textarea = document.querySelector('#chat-form .custom-textarea textarea');
    const formContent = document.querySelector('.phone__form-content');

    if(textarea) {
        textarea.addEventListener('input', function() {
        
            this.style.height = '25px';
            this.style.height = (this.scrollHeight) + 'px';
            
            
            if (this.scrollHeight > 28) {
                formContent.style.minHeight = (this.scrollHeight + 26) + 'px'; 
            } else {
                formContent.style.minHeight = '25px'; 
            }
            
            
            if (this.scrollHeight > 200) {
                formContent.scrollTop = formContent.scrollHeight;
            }
        });
    }
    
}

export{initRubber};