const tops = document.querySelectorAll('.catalog__top');
const bottoms = document.querySelectorAll('.catalog__bottom');

const originalPositions = Array.from(bottoms).map(bottom => bottom.parentNode);

const initMove = () => {
    if (window.innerWidth <= 766) {
        tops.forEach((top, index) => {
            const catalogInfo = top.querySelector('.catalog__info'); 
            const bottom = bottoms[index]; 

            if (bottom && catalogInfo !== bottom.parentNode) { 
                catalogInfo.appendChild(bottom);
            }
        });
    } else {
        
        tops.forEach((top, index) => {
            const bottom = bottoms[index]; 
            if (bottom && originalPositions[index] !== bottom.parentNode) { 
                originalPositions[index].appendChild(bottom);
            }
        });
    }
};

window.addEventListener('resize', initMove);

export { initMove };