const hiddenPass = () => {
    document.querySelectorAll('.js-toggle-password').forEach(button => {
        const input = button.parentElement.querySelector('.js-password-input');
        const icons = button.querySelectorAll('svg');
        
        button.addEventListener('click', function() {
          if (input.type === 'password') {
            input.type = 'text';
            icons[0].classList.add('icon--hidden');
            icons[1].classList.remove('icon--hidden');
          } else {
            input.type = 'password';
            icons[0].classList.remove('icon--hidden');
            icons[1].classList.add('icon--hidden');
          }
        });
      });
    
}

export{hiddenPass};