const initTimer = () => {
    let timeLeft = 44;
    const timerElement = document.getElementById('sms-timer');
    const timerInterval = setInterval(() => {
    timeLeft--;

    if (timerElement) {
        timerElement.textContent = timeLeft;
    }
    
    
    if (timerElement && timerInterval) {
        if (timeLeft <= 0) {
            clearInterval(timerInterval);
            timerElement.textContent = '0';
        }
    }
    
    }, 1000);
}

export{initTimer};