const initMarquee = () => {
    const marqueeContainer = document.querySelector('.marquee');
    const marqueeContent = document.querySelector('.marquee__content');

    if (marqueeContent) {
        const originalSlides = marqueeContent.innerHTML;

        marqueeContent.innerHTML += originalSlides.repeat(2); 
        
        const duplicateSlides = () => {
            marqueeContent.innerHTML += originalSlides;
            adjustAnimationDuration();
        };

        
        const adjustAnimationDuration = () => {
            const slides = marqueeContent.querySelectorAll('.marquee__slide');
            const totalWidth = Array.from(slides).reduce((acc, slide) => acc + slide.offsetWidth, 0);

            
            marqueeContent.style.width = `${totalWidth}px`;

            
            const animationDuration = totalWidth / 150; 
            marqueeContent.style.animationDuration = `${animationDuration}s`;
            marqueeContent.style.animation = `marquee ${animationDuration}s linear infinite`;
        };

        
        adjustAnimationDuration();

        
        setInterval(duplicateSlides, 3000); 
    }

}

export{initMarquee};