const sliderContacts = document.querySelector('[data-slider="contacts"]');
let swiper

const initContactsSlider = () => {
    if (window.innerWidth <= 1278) {
        if (!sliderContacts) {
          return;
        }
    
        if (!swiper) { 
          swiper = new Swiper(sliderContacts, {
            slidesPerView: 1,
            spaceBetween: 20,
            navigation: {
                nextEl: '.swiper-button-next.contacts__next',
                prevEl: '.swiper-button-prev.contacts__prev',
            },
          });
        }
    } else {
        if (swiper) { 
          swiper.destroy();
          swiper = null;
        }
    }

}

window.addEventListener('resize', initContactsSlider);
export{initContactsSlider};