const slider = document.querySelector('[data-slider="catalog"]');

const initInfoSlider = () => {
  if (!slider) {
    return;
  }

  const swiper = new Swiper(slider, {
    slidesPerView: 2,
    spaceBetween: 30,
    // autoHeight: true,
    navigation: {
      nextEl: '.swiper-button-next.catalog__next',
      prevEl: '.swiper-button-prev.catalog__prev',
    },
    breakpoints: {
      280: {
        slidesPerView: 1,
      },
      1023: {
        slidesPerView: 2,
      },
    
    }
  });
};

export {initInfoSlider};
