const initChangeMenu = () => {
    const changeButton = document.getElementById('change');
    const changeSection = document.querySelector('.full2');
    const changeClose = document.querySelector('.full2__close');
    const changeBody = document.querySelector('body');
        
        changeButton.addEventListener('click', () => {
            changeSection.classList.add('open');
            changeBody.classList.add('open');
        })

        changeClose.addEventListener('click', () => {
            changeSection.classList.remove('open');
            changeBody.classList.remove('open');
        })
    

}

export {initChangeMenu};